import React, {useEffect} from 'react';
import {useDispatch, useSelector} from 'react-redux';
import {
  addExpense,
  addIncome,
  clearFields,
  selectFields,
  setActiveCard,
  setAmount,
  setCategory,
  setDate,
  setName,
  setType,
  Transaction,
  TransTypes
} from './CalculateSlice';
import {Button, SegmentedControl, Select, TextInput} from '@mantine/core';
import {nanoid} from '@reduxjs/toolkit'
import {DatePickerInput} from "@mantine/dates";
import './CalculateStyles.css'
import {selectAccountsForCalculate} from "../Accounts/AccountsSlice";

export const incomeCategories: { label: string; value: string }[] = [
  {label: '', value: 'salary'},
  {label: '', value: 'dividends'},
  {label: ' ', value: 'rental'},
  {label: '', value: 'investments'},
  {label: '', value: 'freelance'},
  {label: '', value: 'part-time'},
  {label: '', value: 'bonus'},
  {label: '', value: 'pension'},
  {label: '', value: 'gift'},
  {label: ' ', value: 'otherInc'},

];

export const expenseCategories: { label: string; value: string }[] = [
  {label: '', value: 'groceries'},
  {label: '  ', value: 'eating-out'},
  {label: '', value: 'rent'},
  {label: '', value: 'mortgage'},
  {label: ' ', value: 'utilities'},
  {label: '', value: 'phone'},
  {label: '', value: 'internet'},
  {label: '', value: 'transportation'},
  {label: '', value: 'gas'},
  {label: '  ', value: 'car-payment'},
  {label: '', value: 'insurance'},
  {label: '', value: 'entertainment'},
  {label: '', value: 'travel'},
  {label: '', value: 'clothing'},
  {label: '', value: 'medical'},
  {label: '', value: 'donations'},
  {label: '', value: 'sports'},
  {label: '', value: 'hobbies'},
  {label: '', value: 'electronics'},
  {label: '', value: 'education'},
  {label: ' ', value: 'otherExp'},
];

const FinanceForm: React.FC = () => {
  const dispatch = useDispatch();

  useEffect(() => {

    const localIncomes = localStorage.getItem("income")
    if (localIncomes) JSON.parse(localIncomes).forEach((el: Transaction) =>
      dispatch(addIncome(el))
    )
    const localExpenses = localStorage.getItem("expense")
    if (localExpenses) JSON.parse(localExpenses).forEach((el: Transaction) =>
      dispatch(addExpense(el))
    )

  }, []);

  const accounts = useSelector(selectAccountsForCalculate);
  const {type, activeCard, name, date, amount, category} = useSelector(selectFields);


  const activeCategories = type === "" ? incomeCategories : expenseCategories;

  useEffect(() => {
    dispatch(setCategory(activeCategories[0].value))
  }, [type])

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const financeItem: Transaction = {
      id: nanoid(),
      name,
      category,
      type,
      date: date?.toISOString() ?? '',
      amount: parseFloat(amount),
      account: activeCard
    };

    if (financeItem.type === "") {
      dispatch(addIncome(financeItem));
    } else {
      dispatch(addExpense(financeItem));
    }
    dispatch(clearFields())
    dispatch(setCategory(activeCategories[0].value))
  };

  return (
    <form onSubmit={handleSubmit} className="calculate_form">


      <SegmentedControl
        aria-placeholder="  "
        onChange={(event: TransTypes) => dispatch(setType(event))}
        defaultValue={''}
        data={['', '']}
        radius="lg"
        size="sm"
      />

      <Select
        className="calculate_element"
        required
        label=""
        placeholder="  "
        data={activeCategories}
        value={category}
        onChange={(value: any) => dispatch(setCategory(value))}
      />

      <Select
        className="calculate_element"
        required
        label=""
        placeholder=" "
        data={accounts}
        value={activeCard}
        onChange={(value: any) => dispatch(setActiveCard(value))}
      />

      <TextInput
        className="calculate_element"
        required
        label=""
        type="number"
        placeholder="  "
        min={1}
        step={1}
        value={amount}
        defaultValue={0}
        onChange={(event) => dispatch(setAmount(event.currentTarget.value))}
      />

      <TextInput
        className="calculate_element"
        required
        label=""
        placeholder="  "
        value={name}
        onChange={(event) => dispatch(setName(event.currentTarget.value))}
      />


      <DatePickerInput
        className="calculate_element"
        label=" "
        required
        aria-placeholder="  "
        contextMenu="Date"
        value={date}
        valueFormat="DD MMMM YYYY"
        onChange={(value: Date) => dispatch(setDate(value as Date))}
      />


      <Button
        className="base-button"
        type='submit'
        variant="filled"
        color="teal"
        disabled={!name || !category || !date || !amount}
      >
         
      </Button>
    </form>
  );
};

export default FinanceForm;
